.pagination {
  float: right;
  overflow: hidden;
  display: table;
  margin: 0 auto;
  /*width: 100%;*/
  height: 50px;
}
.pagination li {
  float: left;
  height: 30px;
  border-radius: 5px;
  margin: 3px;
  color: #666;
  list-style: none;
}
.pagination li :hover {
  background: #d84a38;
}
.pagination li :hover a {
  color: #fff;
}
.pagination li a {
  display: block;
  width: 30px;
  height: 30px;
  text-align: center;
  line-height: 30px;
  font-size: 12px;
  border-radius: 5px;
  text-decoration: none;
}
.pagination .active {
  background: #d84a38;
}
.pagination .active a {
  color: #fff;
}
